/*
Sidebar css
*/
.sidebar {
  float: left;
  position: relative;
  width: 18%;
  height: 100vh;
  background: #2c2c35;
  color: #fff;
  border-right: 1.5px solid #393942;

  .header {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1.5px solid #393942;
    .header-text {
      width: 90%;
      text-align: center;
    }
    .header-add-user {
      cursor: pointer;
      margin-left: auto;
      margin-right: 10px;
      &:hover {
        color: #2793c9;
      }
    }
  }

  .user-list {
    height: calc(100vh - 8vw);
    overflow: auto;
    text-align:justify;
    .user {
      cursor: pointer;
      height: 50px;
      display: flex;
      flex-direction: row;
      align-items: center;
      .profile-pic {
        padding: 10px;
        img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          object-fit: cover;
        }
      }
      .user-detail {
        display: flex;
        flex-direction: column;
        font-size: .8em;
        .last-seen {
          font-size: .8em;
          color: #83848F;
        }
      }
      .online-status {
        order: 2;
        margin-left: auto;
        line-height: 32px;
        padding: 10px;
        .online {
          width: 10px;
          height: 10px;
          border-radius: 50px;
          background-color: #31cd31;
        }
        .offline {
          width: 10px;
          height: 10px;
          border-radius: 50px;
          background-color: #83848F;
        }
      }
    }
  }

  .footer {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    left: 0;
    bottom: 0;
    right: 0;
    height: 55px;
    border-top: 1.5px solid #393942;

    .footer-item {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &:hover {
        color: #2793c9;
      }
    }
  }
}
